function arraySplicer(startArray, splices) {
  var newArray = startArray.slice(0)
  var x
  for (x of splices) {
    newArray.splice(x[0],x[1],x[2])
  }
  return newArray
}

function complicatedAltSound(cardID, soundID = '') {
  var standardAltSoundArray = ['A1','EM_G1','EM_G2','S2','EM_O','PA','S1','EM_TH1','EM_T','EM_WP','EM_W']

  // WARRIOR
  // Corrupt Garrosh
  if (['HERO_01b'].includes(cardID)) return 'HERO_01'
  // Nagrand Garrosh, Lu Bao Garrosh, Garrosh of Might, Garrosh of Wrath
  else if (["HERO_01d","HERO_01g",'HERO_01f','HERO_01h'].includes(cardID) && !(standardAltSoundArray.includes(soundID))) return 'HERO_01'
  // Rokara
  else if (['HERO_01i','HERO_01j','HERO_01k'].includes(cardID) && !(standardAltSoundArray.includes(soundID))) return 'HERO_01l'
  // SHAMAN
  // Warchief Thrall
  else if (['HERO_02d'].includes(cardID)) return 'HERO_02'
  // Earthfury Thrall, Thrall Son of Durotan, Alterac Thrall, Wolfrider Thrall, Frostwolf Thrall, Ten Storm Thrall
  else if (['HERO_02f','HERO_02g','HERO_02h','HERO_02i','HERO_02j','HERO_02k'].includes(cardID) && !(standardAltSoundArray.includes(soundID))) return 'HERO_02'
  //ROGUE
  // Cap'n Valeera
  else if (['HERO_03b'].includes(cardID)) return 'HERO_03'
  // Nightslayer Valeera, Diao Chan Valeera, Gladiator Valeera, Deathmantle Valeera
  else if (['HERO_03d','HERO_03e','HERO_03f','HERO_03g'].includes(cardID) && !(standardAltSoundArray.includes(soundID))) return 'HERO_03'
  // PALADIN
  // Lightforged Uther
  else if (['HERO_04d'].includes(cardID)) return 'HERO_04'
  // Second War Uther, Horseman Uther, Uther Lawbringer, Guan Yu Uther, Judgement Uther
  else if (['HERO_04e','HERO_04f','HERO_04g','HERO_04h','HERO_04i'].includes(cardID) && !(standardAltSoundArray.includes(soundID))) return 'HERO_04'
  // HUNTER
  // Wanderer Rexxar
  else if (['HERO_05b'].includes(cardID)) return 'HERO_05'
  // Warsong Rexxar (Weird Set)
  else if (['HERO_05d'].includes(cardID) && !(['A1','EM_G1','S2','EM_O','PA','S1','EM_TH1','T3','EM_T','EM_WP','SP1'].includes(soundID))) return 'HERO_05'
  // Giantstalker Rexxar, Firefang Rexxar, Dragonstalker Rexxar
  else if (['HERO_05e','HERO_05g','HERO_05f'].includes(cardID) && !(standardAltSoundArray.includes(soundID))) return 'HERO_05'
  // DRUID
  // Shan'do Malfurion
  else if (['HERO_06c'].includes(cardID)) return 'HERO_06'
  // Cenarion Malfurion, Storm's Rage Malfurion
  else if (['HERO_06e','HERO_06g'].includes(cardID) && !(standardAltSoundArray.includes(soundID))) return 'HERO_06'
  // WARLOCK
  // Shadow Gul'dan
  else if (['HERO_07c'].includes(cardID)) return 'HERO_07'
  // Felheart Gul'dan, Nemesis Gul'dan
   else if (['HERO_07e','HERO_07f'].includes(cardID) && !(standardAltSoundArray.includes(soundID))) return 'HERO_07'
  // MAGE
  // Fire Mage Jaina
  else if (['HERO_08c'].includes(cardID)) return 'HERO_08'
  // Scholar Jaina, Arcanist Jaina, Apprentice Jaina, Archmage Jaina, Kul Tiran Jaina, Netherwind Jaina
  else if (['HERO_08f','HERO_08g','HERO_08h','HERO_08i','HERO_08j','HERO_08k'].includes(cardID) && !(standardAltSoundArray.includes(soundID))) return 'HERO_08'
  // PRIEST
  // King Anduin
  else if (['HERO_09c'].includes(cardID)) return 'HERO_09'
  // Anduin of Prophecy, SI:7 Anduin, Zhuge Liang Anduin, Transcendence Anduin
  else if (['HERO_09d', 'HERO_09e', 'HERO_09f','HERO_09g'].includes(cardID) && !(standardAltSoundArray.includes(soundID))) return 'HERO_09'
  // Xyrella
  else if (['HERO_09h','HERO_09i','HERO_09j'].includes(cardID) && !(standardAltSoundArray.includes(soundID))) return 'HERO_09k'
  // DEMONHUNTER
  else if (['HERO_10a'].includes(cardID)) return 'HERO_10'
  // Demonbane Illidan, Felravager Illidan
  else if (['HERO_10c','HERO_10d'].includes(cardID) && !(standardAltSoundArray.includes(soundID))) return 'HERO_10'
  // Kurtrus
  else if (['HERO_10e','HERO_10f','HERO_10g'].includes(cardID) && !(standardAltSoundArray.includes(soundID))) return 'HERO_10i'
  else if (['CORE_EX1_323'].includes(cardID)) return 'EX1_323h'
  else return cardID
}

function altSoundsID(cardID) {
  if (['BAR_024'].includes(cardID)) return 'ULD_190'
  else if (['BAR_031'].includes(cardID)) return 'UNG_076t1'
  else if (['BAR_034t3'].includes(cardID)) return 'NEW1_017'
  else if (['BAR_034t4'].includes(cardID)) return 'OG_064a'
  else if (['BAR_034t5'].includes(cardID)) return 'CFM_335'
  else if (['BAR_533t'].includes(cardID)) return 'TRL_546'
  else if (['BAR_538'].includes(cardID)) return 'TRL_309t'
  else if (['BAR_745'].includes(cardID)) return 'ULD_154t'
  else if (['CS3_037'].includes(cardID)) return 'UNG_801'
  // Cards with version groups will always be mapped to the wild (oldest) version's sounds.
  else if (cardVersionGroup(cardID) != undefined) return cardVersionGroup(cardID).wild
  else return cardID
}


